.header {
  position: absolute;
  z-index: 10000;
  width: 100%;
}
.header__container {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  padding-left: 50px;
  padding-right: 50px;
}
.header__dropdown {
  position: relative;
  width: 82px;
  margin-right: 28px;
}
.header__dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin: 2px 0 0;
  padding: 0;
  border: 1px solid #333;
  border-radius: 5px;
  list-style: none;
  z-index: 10000;
}
.header__dropdown-toggle {
  width: 100%;
  padding: 10px;
  background: transparent;
  border-radius: 20px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  padding-left: 15px;
  border: 1px solid transparent;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
}
.header__dropdown-toggle:hover {
  background: #2a2a2a;
  border: 1px solid #64ffda;
}
.header__menu {
  width: 40px;
  height: 32px;
}
.header__wrapper {
  display: flex;
  align-items: center;
}
.header li {
  padding: 5px;
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

.modal__content {
  background: #1e1e1e;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 80%;
  padding: 2rem;
  border-radius: 50px;
  text-align: center;
  z-index: 10001;
  position: relative;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10000;
}

.modal__close {
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
}

.modal__header {
  position: absolute;
  top: calc(0% + 50px);
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal__menu {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.modal__menu li {
  margin: 1rem 0;
}

.modal__menu li a {
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  transition: all 0.3s ease;
}
.modal__menu li a:hover {
  background: linear-gradient(90deg, #fce38a 0%, #f38181 51%, #eaffd0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

.modal__btn {
  border: none;
  background: none;
  outline: none;
  padding: 15px 20px;
  border-radius: 100px;
  background-color: #64ffda;
  border: 1px solid #fff;
  transition: all 0.3s ease;
  font-size: 14px;
  font-family: Karla;
  font-weight: 500;
}
.modal__btn:hover {
  background-color: transparent;
  border: 1px solid #64ffda;
  color: #64ffda;
}

.modal__btn-wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}

@media (max-width: 780px) {
  .header__container {
    padding: 0 15px;
    margin-top: 20px;
  }
  .header__dropdown {
    width: 55px;
  }
  .header__dropdown-toggle {
    font-size: 14px;
    padding: 5px;
  }
  .header__logo {
    width: 112px;
  }
  .header__menu-icon {
    width: 25px;
  }
  .header__vector {
    width: 13px;
  }
  .modal__btn {
    display: none;
  }
  .modal__close {
    position: absolute;
    right: 20px;
  }
  .modal__content {
    justify-content: start;
  }
  .modal__header {
    display: flex;
    align-items: center;
  }
  .modal__item {
    text-align: start;
    margin: 0;
  }
  .modal__logo-img {
    width: 112px;
  }
  .modal__menu {
    align-items: start;
  }
  .modal__menu li {
    margin: 0;
  }
  .modal__menu li a {
    font-size: 18px;
  }
}/*# sourceMappingURL=Header.css.map */